import React from 'react'
import { Link } from 'react-router-dom'

const Header = () => {
  return (
    <header className="bg-blue-800 text-white shadow-md">
      <div className="container mx-auto px-4 py-4 flex justify-between items-center">
        <Link to="/" className="text-2xl font-bold">
           
        </Link>
        <nav className="space-x-4">
          <Link to="/" className="hover:underline"></Link>
          <Link to="/characters" className="hover:underline"></Link>
          <Link to="/categories" className="hover:underline"></Link>
          <Link to="/timeline" className="hover:underline"> </Link>
          <Link to="/about" className="hover:underline"> </Link>
        </nav>
      </div>
    </header>
  )
}

export default Header
